    .bar {
      position: absolute;
      width: 100%;
      height: 3px;
      background-color: #333;
      transition: all 0.3s ease;
    }
    
    /* Position the bars */
    .bar:nth-child(1) {
      top: 0;
    }
    
    .bar:nth-child(2) {
      top: 10px;
    }
    
    .bar:nth-child(3) {
      top: 20px;
    }
  
  /* Transform bars on click */
  .menu-icon.open .bar:nth-child(1) {
    transform: rotate(45deg);
    top: 10px;
  }
  
  .menu-icon.open .bar:nth-child(2) {
    opacity: 0;
  }
  
  .menu-icon.open .bar:nth-child(3) {
    transform: rotate(-45deg);
    top: 10px;
  }
  
  
  /* Responsive Design */
  @media (max-width: 768px) {

    .nav-button {
      display: none;
    }

    .nav-links {
      position: absolute;
      top: 60px;
      left: -100%;
      width: 100%;
      background-color: #fff;
      flex-direction: column;
      align-items: center;
      transition: left 0.3s ease;
    }
  
    .nav-links.active {
      left: 0;
    }
  
    .nav-links li {
      margin: 20px 0;
    }
  
    .menu-icon {
      display: flex;
    }

    .mobile-button {
      background-color: #0077cc;
      color: #fff;
      padding: 8px 20px;
      border: none;
      border-radius: 4px;
      font-size: 16px;
    }
  }
  